
:root {
  --accent: #8b5cf6;
  --bg: #071029;
  --card: #071827;
  --card-alt: #0b1a2b;
  --muted: #9aa6bf;
  --text: #e6eef8;
}

/* Page Layout */
/* Animated Gradient Background */
.page {
  background: linear-gradient(-45deg, #071029, #0b1a2b, #071029, #0b1a2b);
  background-size: 400% 400%;
  animation: gradient 20s ease infinite;

  min-height: 100vh;
  padding: 28px;
  font-family: Inter, system-ui, -apple-system, 'Segoe UI', Roboto;
  color: var(--text);
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

@keyframes gradient {
  0%   { background-position: 0% 50%; }
  50%  { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}


.container {
  width: 100%;
  max-width: 1100px;
  display: flex;
  flex-direction: column;
  gap: 18px;
}

.hero {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
}

.hero-left {
  display: flex;
  align-items: center;
  gap: 12px;
}

.name {
  margin: 0;
  font-size: 24px;
  font-weight: 800;
  color: var(--text);
}

.tagline {
  margin: 6px 0;
  color: var(--muted);
}

.location {
  margin: 0;
  color: var(--muted);
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 13px;
}

/* Contact Buttons */
.contacts-top {
  display: flex;
  gap: 10px;
  align-items: center;
  flex-wrap: nowrap;
  justify-content: flex-end;
  overflow-x: auto;
}

.contact-btn {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 6px 10px;
  border-radius: 8px;
  background: var(--card);
  color: var(--text);
  text-decoration: none;
  border: 1px solid rgba(255,255,255,0.02);
  font-weight: 700;
  font-size: 12px;
  white-space: nowrap;
}

/* Sections / Cards */
.card {
  padding: 16px;
  border-radius: 14px;
  background: var(--card);
  border: 1px solid rgba(255,255,255,0.02);
}
/* Particles / Stars Background */
.particles {
  position: fixed;
  inset: 0;
  z-index: 0;
  pointer-events: none;
  background: transparent url("https://www.transparenttextures.com/patterns/stardust.png");
  animation: drift 120s linear infinite;
  opacity: 0.05; /* keep subtle */
}

@keyframes drift {
  from { background-position: 0 0; }
  to   { background-position: -5000px 5000px; }
}

.section-title {
  margin: 0 0 12px 0;
  color: var(--text);
  font-size: 18px;
}

.text {
  color: var(--muted);
  line-height: 1.5;
}

/* CTA Button */
.cta-primary {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  padding: 8px 12px;
  background: var(--accent);
  color: white;
  border-radius: 10px;
  text-decoration: none;
  font-weight: 700;
}

/* Projects */
.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 12px;
}

.project-card {
  display: flex;
  justify-content: space-between;
  gap: 10px;
  padding: 12px;
  border-radius: 12px;
  background: var(--card-alt);
  text-decoration: none;
  color: var(--text);
  align-items: center;
  transition: transform 160ms ease, box-shadow 160ms ease;
  min-height: 140px;
}

/* Grid Two */
.grid-two {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px;
}

/* Video Row */
.video-row {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 10px;
  border-radius: 10px;
  background: var(--card-alt);
  text-decoration: none;
  color: var(--text);
}

/* Link Inline */
.link-inline {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  color: var(--accent);
  text-decoration: none;
  font-weight: 700;
}

/* Skill Pill */
.skill-pill {
  padding: 8px 10px;
  border-radius: 999px;
  background: rgba(139,92,246,0.06);
  color: var(--muted);
  font-weight: 700;
  font-size: 13px;
  display: inline-block;
  text-decoration: none;
}

/* Footer */
.footer {
  margin-top: 8px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: var(--muted);
  font-size: 13px;
}
